<template>
  <div class="push-message">
    <van-nav-bar
      title="添加员工"
      left-text="返回"
      left-arrow
      @click-left="onClickLeft"
    ></van-nav-bar>
    <div>
      <van-cell-group>
        <van-field v-model="form.userName" placeholder="请输入" label="姓名" />
        <van-field
          v-model="form.phonenumber"
          type="number"
          placeholder="请输入"
          label="手机号"
        />

        <!-- <van-field
          v-model="duties"
          is-link
          readonly
          label="职务"
          placeholder="请选择"
          @click="show = true"
        /> -->
      </van-cell-group>

      <van-cell-group>
        <van-field
          v-model="form.remark"
          rows="2"
          autosize
          label="备注"
          type="textarea"
          maxlength="200"
          placeholder="请输入"
          show-word-limit
        />
      </van-cell-group>
    </div>

    <div class="btn-wrap">
      <van-button type="info" block @click="submitHandler">提交</van-button>
    </div>
  </div>
</template>

<script>
import { addUser } from '@/api/index';
import { mapState, } from 'vuex';
import { checkMobile } from '@/utils/index';


export default {
  data() {
    return {
      duties: [],
      form: {
        userName: '',
        phonenumber: '',
        remark: '',
      }

    };
  },
  computed: {
    ...mapState(['userInfo']),

  },
  methods: {
    onClickLeft() {
      this.$router.go(-1);
    },
    submitHandler() {
      if (!this.form.userName) {
        this.$Toast('请输入用户名');
        return;
      }

      if (checkMobile(this.form.phonenumber).code) {
        this.$Toast('请输入电话号码');
        return;
      }


      addUser({ ...this.form, deptId: this.userInfo.deptId, admin: false }).then((res) => {
        if (res) {
          if (res.data.code === 0) {
            this.$Toast('添加成功');
            this.$router.go(-1);
          } else {
            this.$Toast(res.data.msg);
          }
        } else {
          this.$Toast('添加失败');
        }
      })
    }
  }
};
</script>

<style  lang="less" scoped>
.push-message {
  height: 100%;
  background: #f3f8fc;
  .van-tabs__content {
    margin-top: 12px;
  }

  .van-cell-group {
    margin-top: 12px;
  }

  .btn-wrap {
    width: 90%;
    margin: 10px auto;

    .van-button {
      height: 40px;
      border-radius: 18px;
    }
  }
}
</style>
